@media (max-width: 768px) {
    .wrapper {
        width: 100%;
        padding: 0 15px;
    }

    header {
        flex-direction: column; /* Переключаемся на вертикальное расположение */
        height: auto;
        align-items: center;
        padding: 10px; /* Уменьшаем отступы */
    }

    header .logo {
        margin-bottom: 10px;
    }

    header ul.nav li {
        display: none;
        margin: 5px 10px;
    }

    .login-button, .user-info {
        justify-content: center;
        margin-left: 0;
        margin-top: 70px;
        font-size: 24px;
    }

    main {
        margin-top: 100px;
    }

    .left, .right {
        width: auto;
    }
}

