.steam-login-form {
  text-align: center;
  margin-top: 100px;
  color: #fff;
}

.steam-button {
  background-color: #171a21;
  color: #fff;
  border: none;
  padding: 14px 24px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 10px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  transition: background 0.3s ease;
}

.steam-button i {
  font-size: 20px;
}

.steam-button:hover {
  background-color: #1b2838;
}
