.footer {
  background-color: #0d0d0f;
  color: #f1f1f1;
  padding: 40px;
  width: 100%;
  font-family: 'Montserrat', sans-serif;
  display: flex;
  justify-content: space-around;
}

.footer-container {
  display: flex;
  flex-wrap: wrap;
  gap: 75px;
  max-width: 1200px;
  align-content: center;
  align-items: center;
}

.footer-section {
  flex: 1;
  min-width: 220px;
}

.footer-section h3, .footer-section h4 {
  margin-bottom: 15px;
  font-weight: 600;
}

.footer-section p, .footer-section small {
  opacity: 0.8;
  font-size: 14px;
  line-height: 1.6;
}

.footer-section ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-section ul li {
  margin-bottom: 10px;
  display: flex;
  gap: 6px;
}

.footer-section ul li a {
  text-decoration: none;
  color: #ccc;
  transition: color 0.3s ease;
}

.footer-section ul li a:hover {
  color: #fff;
}

.footer-section hr {
  border: 0;
  border-top: 1px solid #444;
  margin: 15px 0;
}

.credits {
  font-size: 13px;
  opacity: 0.7;
}

.heart {
  color: #e74c3c;
}
